"use client";

import { ChevronLeft, ChevronRight } from "lucide-react";
import { useRef } from "react";
import TourismCard from "@/components/cards/TourismCard";
import Link from "next/link";

const TourismDestinations = () => {
  const scrollContainerRef = useRef<HTMLDivElement>(null);

  const destinations = [
    {
      id: "1",
      name: "Varendra Research Museum",
      type: "Historical",
      entryFee: 50,
      bestTime: "Morning 9 AM - 5 PM",
      rating: 4.5,
      reviewCount: 234,
      imageUrl: "/assets/hero-img.jpg",
      href: "/tourism/1",
    },
    {
      id: "2",
      name: "Puthia Rajbari",
      type: "Heritage",
      entryFee: 30,
      bestTime: "Winter Season, 8 AM - 6 PM",
      rating: 4.8,
      reviewCount: 412,
      imageUrl: "/assets/hero-img.jpg",
      href: "/tourism/2",
    },
    {
      id: "3",
      name: "Padma River Bank",
      type: "Natural",
      entryFee: 0,
      bestTime: "Sunset Time, 5 PM - 7 PM",
      rating: 4.6,
      reviewCount: 189,
      imageUrl: "/assets/hero-img.jpg",
      href: "/tourism/3",
    },
    {
      id: "4",
      name: "Bagha Mosque",
      type: "Religious",
      entryFee: 20,
      bestTime: "Early Morning, 6 AM - 12 PM",
      rating: 4.7,
      reviewCount: 156,
      imageUrl: "/assets/hero-img.jpg",
      href: "/tourism/4",
    },
  ];

  const scroll = (direction: "left" | "right") => {
    if (scrollContainerRef.current) {
      const scrollAmount = 350;
      const newScrollPosition =
        direction === "left"
          ? scrollContainerRef.current.scrollLeft - scrollAmount
          : scrollContainerRef.current.scrollLeft + scrollAmount;

      scrollContainerRef.current.scrollTo({
        left: newScrollPosition,
        behavior: "smooth",
      });
    }
  };

  return (
    <section className="py-12 px-4 bg-gray-50">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <h2 className="text-2xl md:text-3xl font-bold text-gray-900">
            Explore Rajshahi Tourism
          </h2>

          <div className="flex items-center gap-3">
            {/* Navigation Arrows */}
            <div className="hidden md:flex gap-2">
              <button
                onClick={() => scroll("left")}
                className="p-2 rounded-full border border-gray-300 hover:border-gray-400 hover:bg-gray-50 transition"
                aria-label="Scroll left"
              >
                <ChevronLeft className="w-5 h-5 text-gray-600" />
              </button>
              <button
                onClick={() => scroll("right")}
                className="p-2 rounded-full border border-gray-300 hover:border-gray-400 hover:bg-gray-50 transition"
                aria-label="Scroll right"
              >
                <ChevronRight className="w-5 h-5 text-gray-600" />
              </button>
            </div>

            {/* View All Link */}
            <Link
              href="/tourism"
              className="text-blue-600 hover:text-blue-700 font-semibold text-sm flex items-center gap-1 transition"
            >
              View All
              <ChevronRight className="w-4 h-4" />
            </Link>
          </div>
        </div>

        {/* Carousel */}
        <div
          ref={scrollContainerRef}
          className="flex gap-6 overflow-x-auto scrollbar-hide snap-x snap-mandatory scroll-smooth pb-4"
        >
          {destinations.map((destination) => (
            <div
              key={destination.id}
              className="flex-shrink-0 w-[280px] md:w-[320px] snap-start"
            >
              <TourismCard {...destination} />
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default TourismDestinations;
